.container {
  position: relative;
  width: 100%;
  height: 100vh;
  padding-left: 9%;
  padding-right: 9%;
  padding-bottom: 6%;
  padding-top: 4%;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
  overflow: hidden;
}

.background {
  position: relative;
  width: 35%;
  height: 101%;
  /* padding-left: 9%; */
  background: #23252c;
  box-sizing: border-box;
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 2%;
  padding-bottom: 2%;
  margin: 2%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.Angel {
  position: relative;
  /* left: 30%; */
  width: 60%;
  top: 1%;
  /* left: 21%; */
  height: 30%;
  border-radius: 2%;
}

.black-bar-background {
  position: relative;
  width: 61%;
  height: 67%;
  /* left: 20.5%; */
  top: 1.15%;
  border-radius: 2%;
  background: #1a1b21;

  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.titles-background {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  height: 10vh;
  padding: 5%;
}

.name {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  text-align: center;
  font-size: 1.3rem;

  color: #ffffff;
}

.title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 15px;
  text-align: center;

  color: #f3bf99;
}

.quote {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.6rem;
  line-height: 150%;
  /* identical to box height, or 15px */

  text-align: center;

  color: #f5f5f5;
}

.navbar {
  position: relative;
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 10%;
  box-sizing: border-box;
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 35%;
}

.linkedIn,
.resume {
  float: left;
}

.resume,
.linkedIn {
  position: relative;
}

.resumeIcon:hover,
.linkedIn:hover {
  background-color: blue;
}

.p-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 0.9rem;
  line-height: 150%;
  /* or 24px */
  color: #f5f5f5;
}

.p-body {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.72rem;
  line-height: 150%;
  /* or 15px */

  color: #dcdcdc;
  overflow: auto;
}

.footer {
  position: relative;
  width: 61.5%;
  height: 6%;
  /* left: 20.5%; */
  top: -4%;
  background: #161619;
  box-sizing: border-box;
  border-radius: 2%;

  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0%;
  gap: 2%;

  /* top: 90%; */
}

/* DEVICE RESPONSIVE MANAGER */

/* @media only screen and (max-width: 900px) {
  .background {
    top: 100px;
    width: 500px;
    height: 1000px;
  }
} */
@media only screen and (max-width: 359px) {
  .background {
    top: 1vh;
    height: 600px;
    width: 300px;
    background-color: red;
  }
  :root {
    font-size: 1.2vh;
  }
}
@media only screen and (min-width: 360px) and (max-width: 375px) {
  .background {
    top: -8%;
    height: 600px;
    width: 300px;
    overflow: auto;
  }
  :root {
    font-size: 1.3vh;
  }
}
/* PHONE SCREENS */
@media only screen and (min-width: 375px) and (max-width: 820px) {
  .background {
    top: -5vh;
    height: 80vh;
    width: 100%;
  }
  .navbar {
    top: 2vh;
  }
  :root {
    font-size: 1.43vh;
  }
}
/* LAPTOP SCREENS */
@media only screen and (min-width: 820px) and (max-width: 1500px) {
  .background {
    top: 8vh;
    /* height: 1100px; */
    height: 100vh;
    width: 43%;
    min-width: 25rem;
  }
  :root {
    font-size: 2vh;
  }
  .navbar {
    top: 4vh;
  }
  .p-body {
    box-sizing: border-box;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 0.72rem;
    line-height: 150%;
    /* or 15px */

    padding: 1vh;
  }

  .footer {
    position: relative;
    width: 61.5%;
    height: 6%;
    /* left: 20.5%; */
    top: -4%;
    background: #161619;
    box-sizing: border-box;
    border-radius: 2%;

    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0%;
    gap: 2%;

    /* top: 90%; */
  }
}

@media only screen and (min-width: 1500px) {
  :root {
    font-size: 1.5vh;
  }
}
